.date-rank{display:inline-block;vertical-align:top;width:313px;border:1px solid #deb887;border-radius:16px;padding:0 20px 20px;margin-right:5px;font-family:"楷体"}.date-rank .date-rank-list{list-style:none;padding:0;margin:0}.date-rank .date-rank-list li{border-bottom:1px solid #dcdcdc;padding:5px 0}.date-rank .date-rank-list li:last-of-type{border-bottom:0}.date-rank .date-rank-list li .ranklogo{display:inline-block;width:18px;height:20px;margin-right:8px;position:relative;top:4px;background-image:url(./images/rank-medal-4.png);background-size:100% 100%}.date-rank .date-rank-list li .ranklogo.rank-1{background-image:url(./images/rank-medal-1.png)}.date-rank .date-rank-list li .ranklogo.rank-2{background-image:url(./images/rank-medal-2.png)}.date-rank .date-rank-list li .ranklogo.rank-3{background-image:url(./images/rank-medal-3.png)}.date-rank .date-rank-list li p{margin:0 0 0 5px;padding:0;display:inline-block;font-size:12px;color:#b8b8b8}